import { ApolloClient } from "apollo-client"
import { HttpLink } from "apollo-link-http"
import { InMemoryCache } from "apollo-cache-inmemory"
import gql from "graphql-tag"


const client = new ApolloClient({
  link: new HttpLink({ uri: "https://elections2019.xyz/graphql", useGETForQueries: true, headers: {"Content-Type" : "application/graphql" } }),
  cache: new InMemoryCache()
})

export function getVotesDataM(options) {
  if (options.regionType == "national") {
    return client.query({
      query: gql`
      {
        allProvincialBallots(location_Name_Icontains:"Country"){
          edges{
            node{
              topResult(last:${options.numParties}) {
                edges{
                  node{
                    validVotes
                    percOfVotes
                    party {
                      id
                      name
                      abbreviation
                    }
                  }
                }
                
              }
              location {
                id
                name
              }
            }
          }
        }
      }
      `
    })
  } else if (options.regionType == "province") {
    return client.query({
      query: gql`
      {
        allProvincialBallots(location_Name_Icontains:"${options.provinceName}"){
          edges{
            node{
              topResult(last:${options.numParties}) {
                edges{
                  node{
                    validVotes
                    percOfVotes
                    party {
                      id
                      name
                      abbreviation
                    }
                  }
                }
              }
              location {
                id
                name
              }
            }
          }
        }
      }
      `
    })
  } else if (options.regionType == "municipality") {
    return client.query({
      query: gql`
      {
        allMunicipalBallots(location_Province_Name:"${options.provinceName}", 
          event_Description:"2014 National Election",
          location_Name: "${options.muniName}"
        ) {
          edges{
            node {
              topResult(last:${options.numParties}){
                edges{
                  node{
                    party {               
                      name
                      abbreviation
                    }
                    validVotes
                    percOfVotes
                  }
                }
                
              }
              location {
                code
                name
                longName
              }
            }
          }
        }
      }
      `
    })
  } else if (options.regionType == "municipality-vd") {
    return client.query({
      query: gql`
      {
        allVotingDistrictBallots(location_Id:"${options.vdNumber}", 
        event_Description_Icontains:"2014", 
        location_Ward_Municipality_Name_Icontains:"${options.muniCode}") {
          edges{
            node{
              location {
                vdNumber
              }
              topResult(last:${options.numParties}) {
                edges{
                  node{
                    party{
                      name
                    }
                    percOfVotes
                  }
                }  
              }
            }
          }
        }
      }
      `
    })


  }
}

export function getVotesData(options) {
    return client.query({
        query: gql`
        {
          allProvincialBallots(
              event_Description_Icontains:"2004", 
            event_EventType_Description_Icontains:"${options.regionType}",
            location_Name:"${options.regionName}"
        
          ) {
            edges {
              node {
                topResult(validVotes_Gt:1000, last:${options.numParties}) {
                  edges {
                    node {
                      validVotes
                      party {
                        name
                        abbreviation
                      }
                    }
                  }
                }
                location {
                  name
                }
              }
            }
          }
        }
        `,
      });
}